import React from 'react'
import data from './data.json'

const messages =  ["", "", "", "", "[][]", "", "!"]

const Message = () => {
    setTimeout(() => {
        console.log("Display birthday wishes");
    }, 1000);
    return (
        <>
        <div class='messages'>
        {messages.map((message) => (
                    <ul>{message}</ul>
                ))}
        </div>
        </>
    )
}



export default Message
